// src/components/Publications.js
import React, { useState, useEffect } from 'react';
import {
  Box,
  Typography,
  List,
  ListItem,
  Divider,
  Link,
  useTheme,
  useMediaQuery,
} from '@mui/material';
import { motion } from 'framer-motion';

const publications = [
  {
    title: '',
    authors: (
      <>
        <Typography component="span" sx={{ fontWeight: 'bold' }}>
          
        </Typography>
         
      </>
    ),
    venue: '31 SSII2025, IS3-14',
    year: '2025.5.30',
    link: 'https://pub.confit.atlas.jp/ja/event/ssii2025/presentation/IS3-14',
  },
  // 
];

const Publications = () => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));

  // 16:9
  const ASPECT_THRESHOLD = 16 / 9;
  const [isWide, setIsWide] = useState(false);

  useEffect(() => {
    const checkAspect = () => {
      const ratio = window.innerWidth / window.innerHeight;
      setIsWide(ratio >= ASPECT_THRESHOLD);
    };
    checkAspect();
    window.addEventListener('resize', checkAspect);
    return () => window.removeEventListener('resize', checkAspect);
  }, []);

  return (
    <Box id="publications" sx={{ width: '100%', backgroundColor: '#fff' }}>
      {/* 
           
       */}
      <Box
        sx={{
          backgroundColor: '#ffffff',
          py: 6,
          px: 2,
          textAlign: 'center',
        }}
      >
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true, amount: 0.5 }}
          transition={{ duration: 0.6 }}
        >
          <Typography variant="h3" align="center" gutterBottom>
            Publications
          </Typography>
          <Divider sx={{ mb: 3, mx: 'auto', width: 100 }} />
        </motion.div>
      </Box>

      {/* 
           
             minHeight
             
       */}
      <Box
        sx={{
          position: 'relative',
          width: '100%',
          // minHeight padding 
          // padding 
          pt: 4,
          pb: 8,
          px: 2,
          overflow: 'hidden',
          backgroundImage: `url(${process.env.PUBLIC_URL}/images/parallax-mountain.png)`,
          backgroundAttachment: 'fixed',
          backgroundRepeat: 'no-repeat',
          backgroundPosition: 'center top',
          backgroundSize: isMobile
            ? 'auto 100%'   // 100%
            : isWide
            ? '100% auto'  // 100%
            : 'auto 100%',  // 100%
        }}
      >
        {/*  */}
        <Box
          sx={{
            position: 'absolute',
            top: 0,
            left: 0,
            width: '100%',
            // height  padding+ white-overlay 
            height: '100%',
            backgroundColor: 'rgba(255, 255, 255, 0.8)',
            zIndex: 1,
          }}
        />

        {/*  */}
        <Box
          sx={{
            position: 'relative',
            zIndex: 2,
            maxWidth: 800,
            mx: 'auto',
          }}
        >
          {/*  */}
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true }}
            transition={{ duration: 0.5 }}
          >
            <Typography variant="h5" sx={{ fontWeight: 'bold', mb: 2 }}>
              
            </Typography>
          </motion.div>

          {/*  */}
          <List>
            {publications.map((pub, idx) => (
              <motion.div
                key={idx}
                initial={{ opacity: 0, scale: 0.9 }}
                whileInView={{ opacity: 1, scale: 1 }}
                viewport={{ once: true }}
                transition={{ duration: 0.5, delay: idx * 0.15 }}
              >
                <ListItem
                  alignItems="flex-start"
                  sx={{
                    flexDirection: 'column',
                    alignItems: 'flex-start',
                    py: 1,
                  }}
                >
                  {/*  */}
                  <Typography variant="h6" sx={{ fontWeight: 'bold' }}>
                    {pub.title}
                  </Typography>

                  {/*  */}
                  <Typography variant="body2" color="text.secondary" sx={{ mt: 0.5 }}>
                    {pub.authors}
                  </Typography>

                  {/*  */}
                  <Typography variant="body2" color="text.secondary" sx={{ mt: 0.5 }}>
                    {pub.venue}{pub.year}
                  </Typography>

                  {/*  */}
                  {pub.link && (
                    <Link
                      href={pub.link}
                      target="_blank"
                      rel="noopener noreferrer"
                      sx={{ mt: 1, display: 'block' }}
                    >
                      Link
                    </Link>
                  )}
                </ListItem>
                {idx < publications.length - 1 && <Divider sx={{ my: 2 }} />}
              </motion.div>
            ))}
          </List>
        </Box>
      </Box>
    </Box>
  );
};

export default Publications;
