// src/components/MyGallery.js
import React from 'react';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  IconButton,
  useMediaQuery,
  useTheme,
  ImageList,
  ImageListItem,
  Box,
} from '@mui/material';
import CloseIcon from '@mui/icons-material/Close';

/**
 * props:
 *   images: string[]     // URL
 *   onClose: () => void  // 
 */
function MyGallery({ images, onClose }) {
  const theme = useTheme();
  //  sm  cols=2, md  cols=3,  cols=4
  const isXs = useMediaQuery(theme.breakpoints.down('sm'));    // sm 
  const isSm = useMediaQuery(theme.breakpoints.between('sm', 'md')); // smmd
  const isMdUp = useMediaQuery(theme.breakpoints.up('md'));     // md 

  let cols = 4;
  if (isXs) cols = 2;
  else if (isSm) cols = 3;
  else if (isMdUp) cols = 4;

  //  state
  const [selectedImage, setSelectedImage] = React.useState(null);
  const handleClosePreview = () => setSelectedImage(null);

  return (
    <>
      {/*  */}
      <Dialog
        open={true}
        onClose={onClose}
        fullWidth
        maxWidth="lg"
        sx={{
          '& .MuiPaper-root': {
            bgcolor: '#fafafa',   // 
            color: '#333',
          },
        }}
      >
        {/*  */}
        <DialogTitle
          sx={{
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'space-between',
            borderBottom: '1px solid rgba(0, 0, 0, 0.1)',
            backgroundColor: '#fff',
            color: '#333',
            p: '12px 24px',
          }}
        >
          <Box component="span" sx={{ fontSize: '1.25rem', fontWeight: 'bold' }}>
            My Gallery
          </Box>
          <IconButton onClick={onClose} sx={{ color: '#333' }}>
            <CloseIcon />
          </IconButton>
        </DialogTitle>

        {/*  */}
        <DialogContent
          sx={{
            pt: 2,
            pb: 2,
            backgroundColor: '#fafafa',
          }}
        >
          <ImageList cols={cols} gap={8}>
            {images.map((src, idx) => (
              <ImageListItem key={idx}>
                <img
                  src={src}
                  alt={`Gallery ${idx}`}
                  loading="lazy"
                  style={{
                    width: '100%',
                    height: '100%',
                    objectFit: 'cover',
                    borderRadius: 4,
                    boxShadow: '0 2px 8px rgba(0,0,0,0.1)',
                    cursor: 'pointer',
                  }}
                  onClick={() => setSelectedImage(src)}
                />
              </ImageListItem>
            ))}
          </ImageList>
        </DialogContent>
      </Dialog>

      {/*  */}
      {selectedImage && (
        <Dialog
          open={true}
          onClose={handleClosePreview}
          maxWidth="xl"
          fullWidth
          sx={{
            '& .MuiPaper-root': {
              bgcolor: 'rgba(0, 0, 0, 0.95)',
            },
          }}
        >
          <DialogContent
            sx={{
              position: 'relative',
              p: 0,
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
              backgroundColor: 'rgba(255, 255, 255, 0.95)',
              overflow: 'hidden',
            }}
          >
            <IconButton
              onClick={handleClosePreview}
              sx={{
                position: 'absolute',
                top: 8,
                right: 8,
                color: '#333',
                backgroundColor: 'rgba(255,255,255,0.8)',
                '&:hover': { backgroundColor: 'rgba(255,255,255,0.95)' },
                zIndex: 10,
              }}
            >
              <CloseIcon />
            </IconButton>
            <Box
              component="img"
              src={selectedImage}
              alt="Preview"
              sx={{
                maxWidth: '100%',
                maxHeight: '80vh',
                objectFit: 'contain',
                borderRadius: 1,
                boxShadow: '0 4px 16px rgba(0, 0, 0, 0.1)',
              }}
            />
          </DialogContent>
        </Dialog>
      )}
    </>
  );
}

export default MyGallery;
